.hero {
    background-color: var(--overlay);
    color: #fff;
    height: 100vh;
    width: 100%;
    position: relative;
}

.hero:before {
    content: '';
    background: url('./images/hero.jpg') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.hero .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1100px;
    margin: auto;
    padding: 1rem;
}


/* .hero .content p:first-child {
    font-size: .9rem;
    font-style: italic;
} */


.hero .content p:nth-child(1) {
    font-size: 4rem;
    font-weight: 700;
}

.hero .content p:nth-child(2) {
    font-size: 2.2rem;
    font-style: italic;
}

.hero .button {
    margin: 2rem 0;
    width: 300px;
    background-color: rgb(255, 237, 73);
    border: 1px solid var(--secondary-color);
    color: #333;
}

#whatsapp {
    color: green;
    font-size: 1.3rem;
    vertical-align: -2.5px;
    margin-right: 2px;
}

#hero-a-tag {
    width: 300px;
}

.hero .button:hover {
    background-color: var(--secondary-color);
    border: 1px solid var(--secondary-dark);
    box-shadow: 0 0.5em 0.5em -0.5em yellow;
    transform: translateY(-0.20em);
    transition: transform 0.2s ease-in-out;
}

@media screen and (max-width: 940px) {
    .hero .content p:nth-child(3) {
        font-size: 2.6rem;
    }

    .hero .content p:nth-child(4) {
        font-size: 1.6rem;
    }
}